/* eslint-disable @typescript-eslint/no-explicit-any */

/* eslint-disable no-param-reassign */

/* eslint-disable @typescript-eslint/no-shadow */
import { range, quantile } from 'd3-array';
import { axisBottom } from 'd3-axis';
import { format } from 'd3-format';
import { quantize, interpolate, interpolateRound } from 'd3-interpolate';
import {
  ScaleSequential,
  ScaleSequentialQuantile,
  ScaleQuantize,
  ScaleQuantile,
  ScaleThreshold,
  ScaleLinear,
  ScaleOrdinal,
  scaleLinear,
  scaleBand,
} from 'd3-scale';
import { create, Selection, BaseType } from 'd3-selection';

type ScaleThresholdLike<Output> = ScaleQuantize<number> | ScaleQuantile<number> | ScaleThreshold<number, Output>;

type Scale<Output> =
  | ScaleLinear<unknown, Output>
  | ScaleSequential<Output>
  | ScaleSequentialQuantile<Output>
  | ScaleThresholdLike<Output>
  | ScaleOrdinal<number, Output>;
type StringOutputScale = Scale<string>;

function isLinear(scale: StringOutputScale): scale is ScaleLinear<unknown, string> {
  return (scale as any).interpolate;
}

function isSequential(scale: StringOutputScale): scale is ScaleSequential<string> | ScaleSequentialQuantile<string> {
  return (scale as any).interpolator;
}

function isSequentialQuantile(scale: StringOutputScale): scale is ScaleSequentialQuantile<string> {
  return !(scale as any).ticks;
}

function isThresholdLike(scale: StringOutputScale): scale is ScaleThresholdLike<string> {
  return (scale as any).invertExtent;
}

function isQuantize(scale: StringOutputScale): scale is ScaleQuantize<number> {
  return (scale as any).thresholds;
}

function isQuantile(scale: StringOutputScale): scale is ScaleQuantile<number> {
  return (scale as any).quantile;
}

function isOrdinal(scale: StringOutputScale): scale is ScaleOrdinal<number, string> {
  return scale as any;
}

// const scaleX = scaleSequential()
//   .domain([0, 100])
//   .interpolator(interpolateBlues);

export function notEmpty<TValue>(value: TValue | null | undefined | ''): value is TValue {
  return value !== null && value !== undefined && value !== '';
}

// https://observablehq.com/@d3/color-legend#legend

export type Options = {
  title?: string;
  tickSize?: number;
  width?: number;
  height?: number;
  marginTop?: number;
  marginRight?: number;
  marginBottom?: number;
  marginLeft?: number;
  ticks?: number;
  tickFormat?: ReturnType<typeof format> | string;
  tickValues?: number[];
};

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export function Legend(
  color: StringOutputScale,
  {
    title = '',
    tickSize = 6,
    width = 320,
    height = 44 + tickSize,
    marginTop = 18,
    marginRight = 0,
    marginBottom = 16 + tickSize,
    marginLeft = 0,
    ticks = width / 64,
    tickFormat,
    tickValues,
  }: Options = {}
) {
  function ramp(color: (num: number) => string, n = 256) {
    const canvas = document.createElement('canvas');
    canvas.width = n;
    canvas.height = 1;
    const context = canvas.getContext('2d');
    if (context) {
      for (let i = 0; i < n; i += 1) {
        context.fillStyle = color(i / (n - 1));
        context.fillRect(i, 0, 1, 1);
      }
    }
    return canvas;
  }

  const svg = create('svg')
    .attr('width', width)
    .attr('height', height)
    .attr('viewBox', [0, 0, width, height].join(','))
    .style('overflow', 'visible')
    .style('display', 'block');

  let tickAdjust = (
    g: Selection<SVGGElement, unknown, SVGGElement, unknown>
  ): Selection<BaseType, unknown, SVGGElement, unknown> | void =>
    g.selectAll('.tick line').attr('y1', marginTop + marginBottom - height);
  let adjustedScale: any; // (domain: number) => string;

  // Continuous
  if (isLinear(color)) {
    const n = Math.min(color.domain().length, color.range().length);
    // eslint-disable-next-line no-console
    console.log(`d3:${title}: Continuous`, { n });

    adjustedScale = color.copy().rangeRound(quantize(interpolate(marginLeft, width - marginRight), n));

    svg
      .append('image')
      .attr('x', marginLeft)
      .attr('y', marginTop)
      .attr('width', width - marginLeft - marginRight)
      .attr('height', height - marginTop - marginBottom)
      .attr('preserveAspectRatio', 'none')
      .attr('xlink:href', ramp(color.copy().domain(quantize(interpolate(0, 1), n))).toDataURL());
  }

  // Sequential
  else if (isSequential(color)) {
    // eslint-disable-next-line no-console
    console.log(`d3:${title}: Sequential`);

    adjustedScale = Object.assign(color.copy().interpolator(interpolateRound(marginLeft, width - marginRight)), {
      range() {
        return [marginLeft, width - marginRight];
      },
    });

    svg
      .append('image')
      .attr('x', marginLeft)
      .attr('y', marginTop)
      .attr('width', width - marginLeft - marginRight)
      .attr('height', height - marginTop - marginBottom)
      .attr('preserveAspectRatio', 'none')
      .attr('xlink:href', ramp(color.interpolator()).toDataURL());

    // scaleSequentialQuantile doesnâ€™t implement ticks or tickFormat.
    if (isSequentialQuantile(color)) {
      // eslint-disable-next-line no-console
      console.log(`d3:${title}: Sequential Quantile`);
      if (tickValues === undefined) {
        const n = Math.round(ticks + 1);
        tickValues = range(n)
          .map((i) => quantile(color.domain(), i / (n - 1)))
          .filter(notEmpty);
      }
      if (typeof tickFormat !== 'function') {
        tickFormat = format(tickFormat === undefined ? ',f' : tickFormat);
      }
    }
  }

  // Threshold
  else if (isThresholdLike(color)) {
    // eslint-disable-next-line no-console
    console.log(`d3:${title}: Threshold`);
    const thresholds = isQuantize(color)
      ? color.thresholds() // scaleQuantize
      : isQuantile(color)
      ? color.quantiles() // scaleQuantile
      : color.domain(); // scaleThreshold

    const thresholdFormat =
      tickFormat === undefined ? (d: any) => d : typeof tickFormat === 'string' ? format(tickFormat) : tickFormat;

    const scale = scaleLinear()
      .domain([-1, color.range().length - 1])
      .rangeRound([marginLeft, width - marginRight]);

    svg
      .append('g')
      .selectAll('rect')
      .data<number | string>(color.range())
      .join('rect')
      .attr('x', (_, i) => scale(i - 1))
      .attr('y', marginTop)
      .attr('width', (_, i) => scale(i) - scale(i - 1))
      .attr('height', height - marginTop - marginBottom)
      .attr('fill', (d) => d);

    tickValues = range(thresholds.length);
    tickFormat = (i) => typeof i === 'number' && thresholdFormat(thresholds[i]);
    // tickFormat = (i: number) => thresholdFormat(thresholds[i], i);
    adjustedScale = scale;
  }

  // Ordinal
  else if (isOrdinal(color)) {
    // eslint-disable-next-line no-console
    console.log(`d3:${title}: Ordinal`);
    const scale = scaleBand()
      .domain(color.domain().map((value) => `${value}`))
      .rangeRound([marginLeft, width - marginRight]);

    svg
      .append('g')
      .selectAll('rect')
      .data(color.domain())
      .join('rect')
      .attr('x', scale as any)
      .attr('y', marginTop)
      .attr('width', Math.max(0, scale.bandwidth() - 1))
      .attr('height', height - marginTop - marginBottom)
      .attr('fill', color);

    tickAdjust = (): void => {};

    adjustedScale = scale;
  }

  // console.log('Legend', { tickFormat, ticks });
  svg
    .append('g')
    .attr('transform', `translate(0,${height - marginBottom})`)
    .call(
      axisBottom(adjustedScale)
        .ticks(ticks, typeof tickFormat === 'string' ? tickFormat : undefined)
        .tickFormat((typeof tickFormat === 'function' ? tickFormat : undefined) as any)
        .tickSize(tickSize)
        .tickValues(tickValues as any)
    )
    .call(tickAdjust as any)
    .call((g) => g.select('.domain').remove())
    .call((g) =>
      g
        .append('text')
        .attr('x', marginLeft)
        .attr('y', marginTop + marginBottom - height - 6)
        .attr('fill', 'currentColor')
        .attr('text-anchor', 'start')
        .attr('font-weight', 'bold')
        .attr('class', 'title')
        .text(title)
    );

  return svg.node();
}
