import * as React from 'react';

import { max } from 'd3-array';
import { rgb } from 'd3-color';
import { scaleSequential } from 'd3-scale';
import { interpolateBlues } from 'd3-scale-chromatic';

import { AxisLeft, AxisTop } from '@visx/axis';
import { LegendLinear } from '@visx/legend';
import { ParentSize } from '@visx/responsive';
import { scaleLinear, scaleBand } from '@visx/scale';

import { Legend } from './Legend';
import VisxLegend from './VisxLegend';
import { invertColor } from './utils';

export type Props = {
  width: number;
  height: number;
};

const fruits = [
  { name: 'ðŸŠ', count: 21 },
  { name: 'ðŸ‡', count: 13 },
  { name: 'ðŸ', count: 8 },
  { name: 'ðŸŒ', count: 5 },
  { name: 'ðŸ', count: 3 },
  { name: 'ðŸ‹', count: 2 },
  { name: 'ðŸŽ', count: 1 },
  { name: 'ðŸ‰', count: 1 },
];

const MARGIN = { top: 30, right: 0, bottom: 0, left: 30 };

const Content = React.memo((props: Props) => {
  // eslint-disable-next-line
  const { width, height } = props;

  const xScale = React.useMemo(
    () =>
      scaleLinear({
        domain: [0, Math.max(...fruits.map(({ count }) => count))],
        range: [MARGIN.left, width - MARGIN.right],
      }),
    [width]
  );

  const yScale = React.useMemo(
    () =>
      scaleBand({
        domain: fruits.map(({ name }) => name),
        range: [MARGIN.top, height - MARGIN.bottom],
        padding: 0.1,
        round: true,
      }),
    [height]
  );

  const refD3Legend = React.useRef<HTMLDivElement>(null);
  const colorScaleD3 = React.useMemo(
    () =>
      scaleSequential()
        .domain([0, max(fruits, (d) => d.count) || 0])
        .interpolator(interpolateBlues),
    [fruits]
  );
  React.useEffect(() => {
    const svg = Legend(colorScaleD3, { title: 'Number of fruit' });
    if (refD3Legend.current && svg) {
      refD3Legend.current.innerHTML = '';
      refD3Legend.current.appendChild(svg);
    }
  }, []);

  const colorScaleVisx = React.useMemo(
    () =>
      scaleLinear({
        domain: [0, Math.max(...fruits.map(({ count }) => count))],
        range: [interpolateBlues(0), interpolateBlues(1)],
      }),
    [height]
  );

  // console.log('Content', { fruits });
  return (
    <>
      <svg width={width} height={height}>
        <AxisLeft scale={yScale} left={MARGIN.left} />
        <AxisTop scale={xScale} top={MARGIN.top} />
        <g fill="steelblue">
          {fruits.map(({ name, count }, i) => (
            <rect
              key={i}
              y={yScale(name)}
              x={xScale(0)}
              width={xScale(count) - xScale(0)}
              height={yScale.bandwidth()}
              fill={colorScaleVisx(count)}
            />
          ))}
        </g>
        <g fill="white" textAnchor="end" transform={`translate(-6, ${yScale.bandwidth() / 2})`}>
          {fruits.map(({ name, count }, i) => (
            <text
              key={i}
              y={yScale(name)}
              x={xScale(count)}
              dy="0.35em"
              fill={invertColor(rgb(colorScaleVisx(count)).formatHex(), true)}
            >
              {count}
            </text>
          ))}
        </g>
      </svg>
      <div ref={refD3Legend} />
      <VisxLegend color={colorScaleVisx} title="Number of fruit" />
      <LegendLinear scale={colorScaleVisx} />
    </>
  );
});

const Scales = React.memo(() => <ParentSize>{({ width }) => <Content width={width} height={300} />}</ParentSize>);

export default Scales;
