/* eslint-disable @typescript-eslint/no-unused-vars */
import * as React from 'react';

import { format } from 'd3-format';
import { interpolate, interpolateRound, quantize } from 'd3-interpolate';
import { ScaleLinear, ScaleSequential } from 'd3-scale';

import { AxisBottom } from '@visx/axis';

// eslint-disable-next-line @typescript-eslint/no-explicit-any
function isLinear<T>(scale: any): scale is T {
  return Boolean(scale.interpolate);
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
function isSequential<T>(scale: any): scale is T {
  return Boolean(scale.interpolator);
}

// function isLinear<T>(scale: any): scale is T {
//   // eslint-disable-next-line @typescript-eslint/no-explicit-any
//   return Boolean((scale as any).interpolate);
// }

function ramp(color: (num: number) => string, n = 256) {
  const canvas = document.createElement('canvas');
  canvas.width = n;
  canvas.height = 1;
  const context = canvas.getContext('2d');
  if (context) {
    for (let i = 0; i < n; i += 1) {
      context.fillStyle = color(i / (n - 1));
      context.fillRect(i, 0, 1, 1);
    }
  }
  return canvas;
}

export type Props = {
  className?: string;
  children?: React.ReactNode;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  color: any;
  title?: string;
  tickSize?: number;
  width?: number;
  height?: number;
  marginTop?: number;
  marginRight?: number;
  marginBottom?: number;
  marginLeft?: number;
  ticks?: number;
  tickFormat?: ReturnType<typeof format> | string;
  tickValues?: number[];
};

const VisxLegend = React.memo((props: Props) => {
  const {
    className,
    color,
    title = '',
    tickSize = 6,
    width = 320,
    height = 44 + tickSize,
    marginTop = 18,
    marginRight = 0,
    marginBottom = 16 + tickSize,
    marginLeft = 0,
    ticks = width / 64,
  } = props;

  // adjustedScale = color
  //   .copy()
  //   .rangeRound(quantize(interpolate(marginLeft, width - marginRight), n));

  // const scale = React.useMemo(() => {
  //   if (isLinear<ScaleForAxis>(color)) {
  //     console.log('VisxLegend: linear');
  //     const n = Math.min(color.domain().length, color.range().length);
  //     return color
  //       .copy()
  //       .rangeRound(quantize(interpolate(marginLeft, width - marginRight), n));
  //   }
  //   if (isSequential<ScaleSequentialForAxis>(color)) {
  //     console.log('VisxLegend: sequential');
  //     return Object.assign(
  //       color
  //         .copy()
  //         .interpolator(interpolateRound(marginLeft, width - marginRight)),
  //       {
  //         range() {
  //           return [marginLeft, width - marginRight];
  //         },
  //       }
  //     );
  //   }
  //   return undefined;
  // }, [color]);

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const refScale = React.useRef<any>(undefined);
  const [imageUrl, setImageUrl] = React.useState('');
  React.useEffect(() => {
    if (isLinear<ScaleLinear<number, unknown>>(color)) {
      // eslint-disable-next-line no-console
      console.log(`visx:${title}: linear`);
      const n = Math.min(color.domain().length, color.range().length);
      const scaleNext = color.copy().rangeRound(quantize(interpolate(marginLeft, width - marginRight), n));
      refScale.current = scaleNext;
      setImageUrl(ramp(color.copy().domain(quantize(interpolate(0, 1), n)) as ScaleLinear<number, string>).toDataURL());
    }
    if (isSequential<ScaleSequential<string, unknown>>(color)) {
      // eslint-disable-next-line no-console
      console.log(`visx:${title}: sequential`);
      const scaleNext = Object.assign(
        color.copy().interpolator(interpolateRound(marginLeft, width - marginRight))
        // {
        //   range() {
        //     return [marginLeft, width - marginRight];
        //   },
        // }
      );
      refScale.current = scaleNext;
      setImageUrl(ramp(color.interpolator()).toDataURL());
    }
  }, [color]);

  return (
    <svg
      className={className}
      width={width}
      height={height}
      viewBox={[0, 0, width, height].join(',')}
      style={{
        overflow: 'visible',
        display: 'block',
      }}
    >
      <image
        preserveAspectRatio="none"
        x={marginLeft}
        y={marginTop}
        width={width - marginLeft - marginRight}
        height={height - marginTop - marginBottom}
        xlinkHref={imageUrl}
      />
      <g transform={`translate(0, ${height - marginBottom})`}>
        {refScale.current && <AxisBottom scale={refScale.current} numTicks={ticks} />}
        <text
          x={marginLeft}
          y={marginTop + marginBottom - height - 6}
          fill="currentColor"
          textAnchor="start"
          fontWeight="bold"
        >
          {title}
        </text>
      </g>
    </svg>
  );
});

export default VisxLegend;
